:host{
  font-size: 10px;
}
.dav-chipList {
  display: flex;
  gap: 8px;

  &--wrapElements {
      @extend .dav-chipList;
      flex-wrap: wrap;
      padding: 0 8px;
  }

  &--noWrap {
      @extend .dav-chipList;
      position: absolute;
      bottom: 10px;
      align-items: flex-start;
      width: 100%;
      -ms-overflow-style: none;
      scrollbar-width: none;
      overflow: scroll;
      scroll-behavior: smooth;

      &::-webkit-scrollbar {
          display: none;
      }

      &:first-child {
          padding-left: 8px;
      }

      &:last-child {
          padding-right: 8px;
      }
  }
}