/**
 * Component representing a chip list.
 */
import { Component, h, Prop } from '@stencil/core';

@Component({
  tag: 'dav-chip-list',
  styleUrl: 'dav-chip-list.scss',
  shadow: true,
})
export class DavChipList {
  /**
   * Indicates whether the chips should be wrapped.
   * @prop {boolean} wrapChips - Set to `true` to wrap the chips, `false` otherwise. Defaults to `false`.
   */
  @Prop() wrapChips: boolean = false;

  render() {
    return (
      <div class={`dav-chipList ${this.wrapChips ? '--wrapElements' : '--noWrap'}`}>
        <slot></slot>
      </div>
    );
  }
}
